.custom-checkbox__input {
  position: absolute;
  opacity: 0;
}
.custom-checkbox__text {
  display: inline-block;
  position: relative;
  padding-top: 3px;
  padding-left: 33px;
  font-size: 13px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: -1px;
  top: 1px;
  display: inline-block;
  border: 1px solid var(--light-color);
  border-radius: 3px;
  background-color: var(--light-color);
  width: 18px;
  height: 18px;
  box-shadow: inset 0px 4px 8px rgba(0, 0, 0, 0.05);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.custom-checkbox__text::after {
  content: "";
  position: absolute;
  left: -1px;
  top: 1px;
  width: 18px;
  height: 18px;
  border: 1px solid var(--light-color);
  border-radius: 3px;
  background-color: var(--light-color);
  background-image: url("../img/check.svg");
  background-position: center;
  background-size: 10px 10px;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.custom-checkbox__input:checked + .custom-checkbox__text::before {
  background-color: var(--gold-color);
}
.custom-checkbox__input:checked + .custom-checkbox__text::after {
  opacity: 1;
}
.custom-checkbox__input:focus + .custom-checkbox__text {
  outline: 3px solid var(--gold-color);
}

